let pageConfig = {
  pageSize: 10,
  pageNum: 1
};
// import InfiniteLoading from "vue-infinite-loading";
export default {
  components: {
    // InfiniteLoading
  },
  filters: {
    formateTime(t) {
      let date = new Date(t);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      let formatVal = year + "年" + month + "月" + day + "日";
      return formatVal;
    }
  },
  data() {
    return {
      loading: false,
      noMore: false,
      nowTabbarIndex: 0,
      tabList: [],
      bannerList: [],
      articleList: []
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.getTabbarList();
    },
    // 点击顶部tabber
    tapTabbar(e, i, id) {
      console.log(i, id);
      this.nowTabbarIndex = i;
      pageConfig.pageNum = 1;
      this.getArticleList(i);
      this.getBannerList(i);
    },
    // 获取tabbar
    getTabbarList() {
      this.axios.post("/tongue/intf/tongue/getChannels.jsp").then(res => {
        console.log("获取栏目列表==", res.data.data);
        let tabList = res.data.data;
        tabList.sort((a, b) => {
          return a.seqNum - b.seqNum;
        });
        this.tabList = tabList;
        this.getArticleList(0);
        this.getBannerList(0);
      });
    },
    //获取文章列表
    getArticleList(index, other) {
      let params = {
        channelId: this.tabList[index].id,
        pageSize: 10,
        pageNum: 1,
        isTop: 0
      };
      if (other) {
        params.pageNum = pageConfig.pageNum;
        params.pageSize = pageConfig.pageSize;
      } else {
        params.pageNum = 1;
        params.pageSize = 10;
      }
      this.axios
        .post(
          "/tongue/intf/tongue/getArticles.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          this.loading = false;

          console.debug("获取文章列表==", res.data.data);
          let list = res.data.data,
            tempList = this.articleList;
          if (other) {
            list = Array.from(new Set(tempList.concat(list)));
          }
          if (res.data.data.length) {
            // this.$refs.infiniteLoading.$emit("$InfiniteLoading:loaded");
            this.noMore = false;
          } else {
            // this.$refs.infiniteLoading.$emit("$InfiniteLoading:complete");
            this.noMore = true;
          }
          list.forEach((ele, i) => {
            ele.content ? delete ele.content : false;
          });
          this.articleList = list;
        });
    },

    //获取banner
    getBannerList(index, { pageSize = 10, pageNum = 1 } = {}) {
      let params = {
        channelId: this.tabList[index].id,
        pageSize: pageSize,
        pageNum: pageNum,
        isTop: 1
      };
      this.axios
        .post(
          "/tongue/intf/tongue/getArticles.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          console.debug("获取banner==", res.data.data);
          let list = res.data.data;
          this.bannerList = list;
        });
    },
    //
    onInfinite() {
      // if (this.tabList.length <= 0) return;
      console.log(312321321);
      this.loading = true;
      pageConfig.pageNum = pageConfig.pageNum + 1;
      this.getArticleList(this.nowTabbarIndex, pageConfig);
    }
  }
};



