export default {
    state: {
        count: 1
    },
    getters: {
        // store.getters 的值是不能直接修改的 , 需要对应的 state 发生变化才能修改  类似计算属性
        dbCount(state) {
            return state.count * 2
        }
    },
    mutations: {
        //store.commit('MUTATIONS', payload)
        ADD(state) {
            state.count += 1
        }
    },
    actions: {
        // store.dispatch('action', payload)
        DO_ADD(context) {
            context.commit("ADD")
            console.log("actions")
        }
    }
}



