import Vue from "vue"
import Router from "vue-router"
import Index from "@/pages/Index"
import Detail from "@/pages/Detail"
import Regimen from "@/pages/Regimen"
import Article from "@/pages/Article"

Vue.use(Router)

export default new Router({
    routes: [{
            path: "/",
            name: "Index",
            component: Index,
            meta: {
                keep: true
            }
        },
        {
            path: "/detail",
            name: "Detail",
            component: Detail,
            meta: {
                keep: false
            }
        },
        {
            path: "/regimen",
            name: "Regimen",
            component: Regimen,
            meta: {
                keep: true
            }
        },
        {
            path: "/article",
            name: "Article",
            component: Article,
            meta: {
                keep: false
            }
        },
    ]
})



