export default {
  data() {
    return {
      id: "",
      foodData: {}
    };
  },
  created() {
    let id = this.$route.query.id;
    console.log(id);
    this.id = id;
    this.getFoodInfo(id);
  },
  mounted() {},
  methods: {
    //
    back() {
      this.$router.go(-1);
    },
    // 获取食谱详情
    getFoodInfo(id) {
      let params = {
        cId: id
      };
      this.axios
        .post(
          "/tongue/intf/tongue/getCookBooksInfo.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          console.debug("获取食谱详情==", res.data.data);
          let foodData = res.data.data;
          foodData.tag = foodData.effect.split(",");
          let mainKey = [],
            mainVal = [],
            helpKey = [],
            helpVal = [];
          for (let key in foodData.mainIngredient) {
            mainKey.push(`${key}`);
            mainVal.push(foodData.mainIngredient[key]);
          }
          for (let key in foodData.accessories) {
            helpKey.push(`${key}`);
            helpVal.push(foodData.accessories[key]);
          }
          foodData.mainKey = mainKey;
          foodData.mainVal = mainVal;
          foodData.helpKey = helpKey;
          foodData.helpVal = helpVal;
          this.foodData = foodData;
        });
    }
  }
};



