let pageConfig = {
  pageSize: 10,
  pageNum: 1
};
import InfiniteLoading from "vue-infinite-loading";
export default {
  components: {
    InfiniteLoading
  },
  filters: {},
  data() {
    return {
      loading: false,
      noMore: false,
      pickerFlag: false,
      nowTabbarIndex: 0,
      tabList: [],
      bannerList: [],
      physique: "全部体质",
      physiqueTemp: "全部体质",
      physiqueList: [
        {
          flex: 1,
          values: [
            "全部体质",
            "平和体质",
            "阴虚体质",
            "气郁体质",
            "淤血体质",
            "湿热体质",
            "痰湿体质",
            "阳虚体质",
            "气虚体质"
          ],
          textAlign: "center"
        }
      ],
      foodList: []
    };
  },

  created() {
    this.init();
  },
  mounted: function() {
    this.$nextTick(function() {});
  },
  methods: {
    //
    init() {
      this.getFoodCate();
      this.getBannerList();
    },
    //
    go2detail(e, id) {
      this.$router.push({ path: "/detail", query: { id: id } });
    },
    // 点击顶部tabber
    tapTabbar(e, i, id) {
      console.log(i, id);
      this.nowTabbarIndex = i;
      pageConfig.pageNum = 1;
      this.getFoodList();
    },
    // 打开体质选择
    openPicker() {
      this.pickerFlag = true;
    },
    // 关闭体质选择
    closePicker() {
      this.pickerFlag = false;
    },
    // picker事件
    pickerChange(picker, values) {
      // console.log(picker, values)
      this.physiqueTemp = values[0];
    },
    // 确认选择
    surePick() {
      this.physique = this.physiqueTemp;
      pageConfig.pageNum = 1;
      this.getFoodList(this.physiqueTemp);
      this.closePicker();
    },
    // 获取食谱类别
    getFoodCate() {
      this.axios.post("/tongue/intf/tongue/getCookBookCates.jsp").then(res => {
        console.log("获取食谱类别==", res.data.data);
        this.tabList = res.data.data;
        this.getFoodList();
      });
    },
    // 食谱banner
    getBannerList() {
      let params = { adsenseId: "A00107d3755e7c614afaab80bd74e388f3c6" };
      this.axios
        .post(
          "/tongue/intf/tongue/getMaterials.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          console.debug("获取食谱banner===", res.data.data);
          let bannerList = res.data.data;
          if (bannerList.length > 0) {
            bannerList.forEach((ele, i) => {
              if (ele.path) {
                ele.fid = ele.path.split("=")[1];
              }
            });
          }

          console.debug("bannerList", bannerList);
          this.bannerList = bannerList;
        });
    },
    // 获取食谱列表
    getFoodList(physique, other) {
      const self = this;
      let params = {
        physique: physique || "",
        pageSize: 10,
        pageNum: 1,
        cid: this.tabList[this.nowTabbarIndex].id
      };
      if (params.physique == "全部体质") {
        params.physique = "";
      }
      if (other) {
        params.pageNum = pageConfig.pageNum;
        params.pageSize = pageConfig.pageSize;
      } else {
        params.pageNum = 1;
        params.pageSize = 10;
      }
      this.axios
        .post(
          "/tongue/intf/tongue/getCookBooks.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          this.loading = false;
          console.debug("获取食谱列表==", res.data.data);
          let list = res.data.data,
            tempList = this.foodList;
          if (other) {
            list = Array.from(new Set(tempList.concat(list)));
          }
          if (res.data.data.length) {
            // this.$refs.infiniteLoading.$emit("$InfiniteLoading:loaded");
            this.noMore = false;
          } else {
            // this.$refs.infiniteLoading.$emit("$InfiniteLoading:complete");
            this.noMore = true;
          }

          list.forEach((ele, i) => {
            ele.effect = ele.effect.replace(/\,/g, "、");
            ele.steps ? delete ele.steps : false;
            ele.accessories ? delete ele.accessories : false;
            ele.desc ? delete ele.desc : false;
            ele.mainIngredient ? delete ele.mainIngredient : false;
          });
          this.foodList = list;
        });
    },
    //
    onInfinite() {
      if (this.tabList.length <= 0) return;
      this.loading = true;
      pageConfig.pageNum = pageConfig.pageNum + 1;
      this.getFoodList(this.physique, pageConfig);
    }
  }
};



