//  type 1 图文 2 视频  3 音频
export default {
  filters: {
    formatTime(t) {
      let date = new Date(t);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      let formatVal = year + "年" + month + "月" + day + "日";
      return formatVal;
    },
    formatT(t) {
      let date = new Date(t);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const minute = date.getMinutes();
      let formatVal =
        year +
        "年" +
        month +
        "月" +
        day +
        "日" +
        " " +
        (hour > 9 ? hour : "0" + hour) +
        ":" +
        (minute > 9 ? minute : "0" + minute);
      return formatVal;
    }
  },
  data() {
    return {
      articleData: {},
      articleList: [],
      commentList: []
    };
  },
  watch: {
    $route(to, from) {
      if (to.name === "Article") {
        console.log(to.query); // 在此调用函数
        this.init();
        this.backTop()
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    //
    init() {
      let id = this.$route.query.id;
      console.log(id);
      this.id = id;
      this.getArticleInfo(id);
    },
    //
    backTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    // 
    back(){
      this.$router.go(-1)
    },
    // 获取文章详情
    getArticleInfo(aid) {
      let params = {
        aId: aid
      };
      this.axios
        .post(
          "/tongue/intf/tongue/getArticlesInfo.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          console.debug("获取文章详情==", res.data.data);
          let articleData = res.data.data;
          this.articleData = articleData;

          // 获取推荐文章
          this.getArticleList(articleData.channel.id);
          // 获取评论
          this.getCommentList(this.id);
        });
    },
    //获取文章列表
    getArticleList(channelId) {
      let params = {
        channelId: channelId,
        pageSize: 10,
        pageNum: 1,
        isTop: 0
      };
      this.axios
        .post(
          "/tongue/intf/tongue/getArticles.jsp",
          new URLSearchParams(params)
        )
        .then(res => {
          console.debug("获取文章列表==", res.data.data);
          let list = res.data.data;
          this.articleList = list;
        });
    },
    // 评论相关
    //  act(add（新增）, list(列表），like(点赞）
    getCommentList(aid) {
      let params = {
        act: "list",
        userId: "U0013d4509ceb00d4245834c8bc9adc89622",
        aId: aid,
        pageSize: 20,
        pageNum: 1
      };
      this.axios
        .post("/tongue/intf/tongue/comment.jsp", new URLSearchParams(params))
        .then(res => {
          console.log("评论相关  ", res.data.data);
          let list = res.data.data;
          this.commentList = list;
        });
    },
    // 跳转
    go2article(e, id) {
      this.$router.push({ path: "/article", query: { id: id } });
    }
  }
};



